<template>


   <div>
      <div class="header">
    <ul class="header-button-left">
      <li v-if="눌렀냐 != 0" @click="눌렀냐=0">Cancel</li>
    </ul>
    <ul class="header-button-right">
      <li v-if="눌렀냐 == 1" @click="눌렀냐++">Next</li>
      <li v-if="눌렀냐 == 2" @click="publish">발행</li>
    </ul>
  </div>

  <Container :인스타글="인스타글" :눌렀냐="눌렀냐" 
  :필터종류="필터종류" :업로드이미지="업로드이미지" :선택한필터="선택한필터" @uploadBoard="작성한글 = $event"/>

  <div class="footer" v-if="눌렀냐 == 0">
    <ul class="footer-button-plus">
      <input type="file" id="file" class="inputfile"
      @input="upload" />
      <label for="file" class="input-plus">+</label>
    </ul>
 </div>
   </div>

   <button v-if="눌렀냐 == 0" @click="more">더보기</button>

   

</template>

<script>
import axios from 'axios';
import Container from './containerView.vue';
import instaData from './../assets/insta.js';
import filterNames from './../assets/filterNames.js'
export default {
   setup () {
      

      return {}
   },
   data(){
    return{
      인스타글 : instaData,
      더보기 : 0,
      눌렀냐 : 0,
      업로드이미지 : '',
      작성한글:'',
      필터종류 : filterNames,
      선택한필터 : '',
    }
   },
   components:{
    Container,
   },
   methods:{
    more(){
      axios.get(`https://codingapple1.github.io/vue/more${this.더보기}.json`).then(결과=>{
        this.인스타글.push(결과.data)
        this.더보기++
      })
    },upload(e){
      let 파일 = e.target.files;
      let url = URL.createObjectURL(파일[0])
      this.업로드이미지 = url 
      this.눌렀냐++
    },
    publish(){
      let 내게시물 = {
        name: "doyeon",
        userImage: "https://picsum.photos/100?random=3",
        postImage: this.업로드이미지,
        likes: 36,
        date: "May 15",
        liked: false,
        content: this.작성한글,
        filter: this.선택한필터
      };
      this.인스타글.unshift(내게시물)
      this.눌렀냐 = 0
    }
    },
    mounted(){
      this.emitter.on('박스클릭함',(a)=>{
        this.선택한필터 = a;
      })
    }
   }

</script>

