<template>
<div class= "button-wrapper">
  <button @click = "뷰동산보기 = true; 
  뷰블로그보기 = false; 뷰스타그램보기=false">show vuedongsan</button>


  <button @click = "뷰블로그보기 = true; 
  뷰동산보기 = false;  뷰스타그램보기=false">show vueblog</button>

  <button @click = "뷰스타그램보기 = true; 
  뷰동산보기 = false ; 뷰블로그보기 = false">show vuestagram</button>

  <button @click = "뷰동산보기 = false; 뷰스타그램보기=false; 뷰블로그보기 = false ">hide</button>
</div>

<p>{{ name }}</p>
<p>{{ age }}</p>
<p>{{ likes1 }}</p>
<button @click="liked">++</button>

<VuedongsanView v-if="뷰동산보기 == true"
:네브="네브" :카운트다운="카운트다운" :신고="신고"
:방정보="방정보" :룸인포="룸인포" :모달창열림="모달창열림" :배너열림="배너열림"
@openBanner="배너열림 == true" @closeModal="모달창열림 = false" @report="신고++"/>
<div v-if="뷰블로그보기 == true"><router-view :블로그글="블로그글"></router-view></div>

<Vuestagram v-if="뷰스타그램보기 == true"/>
</template>

<script>
import roomInfo from './assets/roomInfo.js';
import blog from './assets/blog.js';
import VuedongsanView from './components/VueDongsanView.vue';
import Vuestagram from './components/vueStagram.vue';
import {mapMutations, mapState} from 'vuex'
export default {
  data () {
    return {
      네브 : ['Home','Shop','About'],
      신고 : 0,
      모달창열림 : false,
      방정보 : roomInfo,
      누른거 : 0,
      month:1,
      방정보오리지널 : [...roomInfo],
      카운트다운 : 30,
      배너열림 : true,
      뷰동산보기 : false,
      뷰블로그보기 : false,
      블로그글 : blog,
      뷰스타그램보기 : false,
    }
  },
  components:{
    VuedongsanView,
    Vuestagram,
  },
  watch:{
    month(a){
      if(isNaN(a) == true){
        alert('1~12사이 입력해야함')
        this.month = 1;
      }
      else if(a >= 13){
        alert('13이상 입력안됨')
      }
    }
  },
  methods:{
    ...mapMutations(['setMore','liked']),
    priceSort(){
      this.방정보.sort(function(a,b){
        return a.price-b.price
      })
    },
    reverseSort(){
      this.방정보.sort(function(a,b){
        return b.price-a.price
      })
    },
    abcSort(){
      this.방정보.sort(function(a,b){
        if(typeof a == 'string') a = a.toUpperCase();
        if(typeof b == 'string') b = b.toUpperCase();

        return a> b? 1: -1;
      })
    },
    sortBack(){
      this.방정보 = [...this.방정보오리지널]
    },
  },mounted(){
    setInterval(() => {
      this.카운트다운 = this.카운트다운--
    }, 1000);
    setTimeout(() => {
      this.배너열림 = false;
    }, 30000);
  },
  computed:{
    name(){
      return this.$store.state.name
    },
    ...mapState(['name','age','likes1']),
  }
}
</script>

